.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.masonry {
  column-count: 4;
  column-gap: 1em;
}

.XS {
  .masonry {
    column-count: 1;
  }
}

.SM {
  .masonry {
    column-count: 2;
  }
}

.MD {
  .masonry {
    column-count: 3;
  }
}

.LG {
  .masonry {
    column-count: 4;
  }
}

.XL {
  .masonry {
    column-count: 4;
  }
}

.XXL {
  .masonry {
    column-count: 5;
  }
}

.location .slick-slide {
  padding: .5em;
}
