.left {
  background-color: #2f54eb;
  height: 100%;
  z-index: -2;
}

.leftText {
    display: inline-block;
    text-align: left;
    margin-top: 1em;
    margin-bottom: 1em;
    color: white;
    font-size: 1.5em;
    font-weight: 500;
}
.right {
  z-index: 1;
  background-color: white;
  height: 100%;

  h1 {
    font-size: 36px;
    font-weight: 600;
    line-height: 40px;
    margin: 8px 0 28px;
  }

  label {
      float:left;
  }
}
.icon {
    margin-right: 1em;
}

.center {
    margin-right: auto;
    margin-left:auto;
}
.overlay {
  height: 100%;
  left: -100%;
  position: absolute;
  z-index: -1;
  opacity: 0.1;
}

.rowContainer {
    margin-top: 15px;
    margin-bottom: 15px;
}
.inline {
    display: inline-block;
}