.card {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.09);
  margin-bottom: 18px;
}

.card .slick-slide {
  padding: 0;
}

.itemTitle {
  font-size: 2em;
  margin-bottom: 0;
}

.noReviews {
  color: rgba(0, 0, 0, 0.45);
}

.carousel {
  min-height: 200px;
  height: 200px;
  overflow: hidden;
}

.img {
  width: 100%;
}

.input {
  position: sticky;
  top: 0px;
}

.container {
  border-bottom: solid 1px #e8e8e8;
  padding: 15px;
  padding-bottom: 25px;
  margin-bottom: 20px;
  position: sticky;
  display: block;
  top: 0px;
  z-index: 1060;
  background-color: white;
  height: 100%;
}

.containerNoBorder {
  padding: 15px;
  margin-bottom: 20px;
}
