.sticky {
  position: sticky;
  top: 0;
  z-index: 2000;
  background-color: white;
}

.hidden {
  height: 0px;
  -webkit-transition: height, 0.5s linear;
  -moz-transition: height, 0.5s linear;
  -ms-transition: height, 0.5s linear;
  -o-transition: height, 0.5s linear;
  transition: height, 0.5s linear;
}
.open {
  height: 200px;
  -webkit-transition: height, 0.5s linear;
  -moz-transition: height, 0.5s linear;
  -ms-transition: height, 0.5s linear;
  -o-transition: height, 0.5s linear;
  transition: height, 0.5s linear;
}

.stickyBackMenu {
  position: sticky;
  top: 0;
  height: 50px;
  width: 100%;
  z-index: 1001;
  background-color: white;
}

.stickySelect {
  position: sticky;
  top: 50px;
  width: 100%;
  height: 50px;
  z-index: 1001;
  margin-bottom: 1.5em;
  background-color: white;
  border-bottom: 1px solid #e8e8e8;
}

.noReviews {
  color: rgba(0, 0, 0, 0.45);
}
.ratingContainer {
  width: 100%;
  margin-bottom: 20px;
}

.section {
    margin-top: 1em;
    margin-bottom: 1em;
}