.splitPane {
  position: fixed;
  width: calc(100vw - 200px);
  height: calc(100vh - 115px);
  min-height: calc(100vh - 115px);
  max-height: calc(100vh - 115px);
  left: 200px;
  top: 65px;
}

.content {
  position: fixed;
  width: 100vw;
  height: calc(100vh - 115px);
  top: 65px;
}

.panel {
  overflow-y: scroll;
  width: 100%;
}

.overlay {
  height: 100%;
  top: 10%;
  left: -100%;
  position: absolute;
  z-index: -1;
  opacity: 0.02;
}

.footer {
  height: 50px;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #314056;
}