.header {
  font-size: 8vh;
  font-weight: 600;
  line-height: 76px;

  @media screen and (max-width: 576px) {
    font-size: 7.5vh;
  }
}
.sub {
  font-size: 3.5vh;
  line-height: 40px;
  color: #314659;
}

.container {
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  margin-bottom: 50px;
}

.overlay {
  height: 100%;
  width: 50%;
  top: -10%;
  left: -10%;
  position: absolute;
  opacity: 0.2;

  @media screen and (max-width: 576px) {
    left: -50%;
    top: -15%;
    width: 100%;
  }
}

.overlay2 {
  height: 100%;
  width: 60%;
  bottom: -50%;
  right: -20%;
  position: absolute;
  opacity: 0.2;

  @media screen and (max-width: 576px) {
    display: hidden;
  }
}

.slant {
  background-color: white;
  clip-path: polygon(0 0, 100% 0, 100% calc(100% - 6vw), 0 100%);
  padding-bottom: 100px;
}