.ratingContainer {
  width: 100%;
  margin-bottom: 20px;
}

.cardContainer {
  margin-top: 20px;
  height: 22px;
}

.noReviews {
  color: rgba(0, 0, 0, 0.45);
}

.tagStyle {
  margin-bottom: 5px;
}

.item {
  /* Masonry bricks or child elements */  
  position: relative;
  margin: 0 0 1em;
  width: 100%;

  &.ant-card-meta-title {
    white-space: normal;
  }
}

.carousel {
  min-height: 200px;
  height: 200px;
  overflow: hidden;
  z-index: 0;
}

.img {
  min-width: 100%;
  min-height: 200px;
  margin-left: auto;
  margin-right: auto;
}
