.resultList {
  padding-left: 1.5em;
  padding-right: 1.5em;
  display: block;
  overflow-y: scroll;
}

.discoverMenu {
  display: block;
  position: absolute;
  top: 45px;
  width: 100%;
}

.container {
  height: 100%; /*allows both columns to span the full height of the browser window*/
  display: flex;
  flex-direction: column;
}

.header {
  display: absolute;
  padding-top: 5px;
  top: 0;
}

.content {
  min-height: calc(100vh - 160px);
  max-height: calc(100vh - 160px);
  overflow-y: auto; /*adds scroll to this container*/
  padding: 0;
}

.sticky {
  width: 100%;
  position: sticky;
  top: 0;
  background-color: white;
  z-index: 2000;
}
