.card {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.09);

  &:active {
    background-color: #e6f7ff;
  }
}

.cardEdit {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.09);
}

.doneMenuItem {
  color: white;

  :hover {
    color: white;
  }

  :active {
    color: white;
  }
}

.titleInput {
  border: none;
  border-bottom: 1px solid #757575;
  font-size: 2em;
  overflow: visible;
  width: 60%;
  margin-bottom: 10px;

  &:focus {
    outline: none;
    border-color: #1890ff;
  }
}

.section {
  margin-top: 15px;
  margin-bottom: 15px;
}

.quill {
  margin-top: 10px;
  margin-bottom: 10px;
  height: 200px;
}

.borderless {
  margin: 0;
}

.menuItem {
  &.ant-menu-item-selected {
    &i {
      color: white;
    }
  }
}

.itemTitle {
  font-size: 2em;
  margin-bottom: 0;
  width: 100%;
  max-width: 100%;
  word-wrap: break-word;
  hyphens: auto;
}

.gallery img {
  border-radius: 8px;
}

.tagStyle {
  margin-bottom: 5px;
}

.card:hover .hidden {
  display: block;
}

.hidden {
  display: none;
  position: absolute;
  right: 10px;
  top: 10px;
  z-index: 2000;
}
