.logo {
  height: 31px;
  float: left;
}
.logoIcon {
  height: 100%;
  margin-right: 10px;
}

.logoText {
  text-decoration: none;
  font-size: 23px;
  font-weight: 600;
  display: inline;
  color: #337ab7;

  &:visited {
    color: #337ab7;
    text-decoration: none;
  }
}

.right {
  float: right;
}

.menuCon {
  line-height: 64px;
  height: 64px;
  float: right;
  width: auto;
}

.menuBar {
  border-bottom: solid 1px #e8e8e8;
  padding-left: 20px;
  padding-right: 20px;
  height: 65px;
}

.iconButton {
  border: none;
}
