.list {
  max-height: calc(100vh - 250px);
  overflow-y: auto;
}

.multilist {
  max-height: calc(100vh - 115px);
  min-height: calc(100vh - 115px);
}

.addSection {
  min-height: 10px;
  cursor: pointer;
  opacity: 0;
  text-align: center;
  padding:0;

  &:hover {
    padding: 15px;
    opacity: 1;
  }
}
