.masonry {
  column-count: 4;
  column-gap: 1em;
}

.XS {
  .masonry {
    column-count: 1;
  }
}

.SM {
  .masonry {
    column-count: 2;
  }
}

.MD {
  .masonry {
    column-count: 3;
  }
}

.LG {
  .masonry {
    column-count: 4;
  }
}

.XL {
  .masonry {
    column-count: 4;
  }
}

.XXL {
  .masonry {
    column-count: 5;
  }
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 2rem;
}
