.bigContainer {
  padding-top: 140px;
  padding-bottom: 140px;
  background-color: #f0f3f6;
  text-align: center;
}

.title {
  font-size: 38px;
  line-height: 46px;
  color: #314659;
  text-align: center;
  font-weight: 400;
  padding-left: 30px;
  margin-bottom: 20px;
  padding-right: 30px;
}

.subTitle {
  font-size: 28px;
  margin: 0 auto 32px;
  color: black;
  margin-bottom: 50px;
}

.form {
  input {
    width: 80%;
    padding: 6px 11px;
    height: 50px;
    font-size: 16px;
    line-height: 1.5;
    color: rgba(0, 0, 0, 0.65);
    background-color: #fff;
    background-image: none;
    border: 1px solid #d9d9d9;
    border-radius: 4px;
  }

  button {
    padding: 0 15px;
    margin-left: 10px;
    font-size: 16px;
    border-radius: 4px;
    height: 50px;
    color: #fff;
    background-color: #1890ff;
    border-color: #1890ff;
    text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.12);
    -webkit-box-shadow: 0 2px 0 rgba(0, 0, 0, 0.045);
    box-shadow: 0 2px 0 rgba(0, 0, 0, 0.045);
  }
}
